.top {
  width: 100%;
  height: 50px;

  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  font-family: "Josefin Sans", sans-serif;
  justify-content: space-between;
  z-index: 999;
}
.topCenter {
  width: 50%;
}

.topRight,
.topLeft {
  width: 25%;
  display: flex;
  align-items: center;
}

.toplist {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  justify-content: center;
}
