.listItems {
  font-weight: 300;
  font-size: 18px;
  margin-right: 20px;
  cursor: pointer;
}
.topSearch {
  font-size: 19px;
  color: #666;
  cursor: pointer;
  margin-left: 150px;
}
.topIcon {
  margin-left: 0px;
}
